.leaderboard-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.search-controls {
    display: flex;
}

.select {
    box-sizing: border-box;
    width: 200px;
    padding: 5px;
    z-index: 0;
}

@media screen and (max-width: 1000px) {
    .search-controls {
        flex-direction: column;
    }
}